

define('pages/guest_signup',['backbone', 'Handlebars', 'text!./guest_signup.hbs', 'text!./guest_signup_personality.hbs', 'entities/cur_user_personality', 'entities/user_personality_skinny', 'ui_widgets/psst', 'core'], function(Backbone, Handlebars, tmpl, tmplPersonality, CurUserPersonality, UserPersonalitySkinny, psst, app) {
  var Me, _coreGameId, _resultsSel, _signupSel;
  _coreGameId = 1;
  _resultsSel = '#ResultsContainer';
  _signupSel = '#SignupContainer';
  Me = Backbone.View.extend({
    tmplPersonality: Handlebars.compile(tmplPersonality),
    className: 'guestSignupPage',
    events: {
      "click #ActionShowSignup": "onClickedSignUp"
    },
    initialize: function() {
      this.personalityFull = new CurUserPersonality();
      this.listenTo(this.personalityFull, 'sync', this.onSync);
      this.listenTo(this.personalityFull, 'error', this.onError);
      return this.listenTo(app.user, 'sync', this.onUserSync);
    },
    render: function() {
      this.$el.html(tmpl);
      return this;
    },
    _renderPersonalityBrief: function(model) {
      return this.$(_resultsSel).html(this.tmplPersonality(model.attributes));
    },
    onSync: function(model) {
      var personalityBrief, profile;
      profile = model.get('profile_description');
      personalityBrief = new UserPersonalitySkinny({
        name: profile.name,
        one_liner: profile.one_liner,
        logo_url: profile.logo_url
      });
      return this._renderPersonalityBrief(personalityBrief);
    },
    onError: function(model, xhr) {
      return psst({
        sel: _resultsSel,
        title: "Doh.",
        msg: "Trouble getting a read on your personality. " + xhr.status + ": " + xhr.statusText,
        type: psst.TYPES.error
      });
    },
    onClickedSignUp: function() {
      app.analytics.track(this.className, 'Pressed Sign Up');
      return app.trigger('session:showRegister');
    },
    onUserSync: function(userModel) {
      console.log('user registered or signed in');
      if (userModel.isGuest()) {
        return console.log('somehow they\'re still a guest');
      } else {
        return app.router.navigate('dashboard', {
          trigger: true
        });
      }
    }
  });
  return Me;
});
