

define('models/result',['jquery', 'backbone'], function($, Backbone) {
  var Result;
  Result = Backbone.Model.extend({
    urlRoot: function() {
      var assessment_id;
      assessment_id = this.get('assessment_id');
      return "" + window.apiServerUrl + "/api/v1/users/-/games/" + assessment_id + "/result";
    },
    calculateResult: function() {
      var deferred,
        _this = this;
      deferred = $.Deferred();
      this.startCalculation().done(function() {
        return _this.checkForProgress(true, null).done(function() {
          return _this.fetch().done(function(data, textStatus, jqXHR) {
            return deferred.resolve();
          }).fail(function(jqXHR, textStatus, errorThrown) {
            return deferred.reject();
          });
        }).fail(function() {
          return deferred.reject();
        });
      }).fail(function() {
        return deferred.reject();
      });
      return deferred.promise();
    },
    startCalculation: function() {
      var attrs, deferred,
        _this = this;
      deferred = $.Deferred();
      this.timeoutAttempt = 0;
      attrs = {};
      this.save(attrs).done(function(data, textStatus, jqXHR) {
        var statusCode;
        console.log("Start Calculation Success: " + textStatus);
        statusCode = jqXHR.statusCode();
        if (statusCode !== 202) {
          console.log("Unexpected StatusCode " + statusCode);
        }
        _this.progressLink = data.status.link;
        return deferred.resolve(jqXHR.response);
      }).fail(function(jqXHR, textStatus, errorThrown) {
        console.log("Start Calculation Error: " + textStatus);
        return deferred.reject(textStatus);
      });
      return deferred.promise();
    },
    checkForProgress: function(firstTime, deferred) {
      var _this = this;
      if (firstTime) {
        deferred = $.Deferred();
      }
      if (this.progressLink != null) {
        $.ajax({
          type: 'GET',
          url: this.progressLink
        }).done(function(data, textStatus, jqXHR) {
          var statusCode;
          console.log("Check For Progress Success: " + textStatus);
          statusCode = jqXHR.statusCode();
          if (statusCode !== 200) {
            console.log("Unexpected StatusCode " + statusCode);
          }
          switch (data.status.state) {
            case 'pending':
              _this.progressLink = data.status.link;
              console.log("Still pending for results");
              _this.timeoutAttempt += 1;
              if (_this.timeoutAttempt === 4) {
                console.log("Timeout limit is reached!");
                return deferred.reject("Timeout limit is reached!");
              } else {
                return setTimeout(function() {
                  return _this.checkForProgress(false, deferred);
                }, 500);
              }
              break;
            case 'done':
              console.log("Done with results");
              return deferred.resolve(jqXHR.response);
            case 'error':
              console.log("Server error, no results");
              return deferred.reject(textStatus);
            default:
              console.log("Unexpected status " + data.status.state);
              return deferred.reject(textStatus);
          }
        }).fail(function(jqXHR, textStatus, errorThrown) {
          console.log("Check For Progress Error: " + textStatus);
          return deferred.reject(textStatus);
        });
      } else {
        console.log("No url to check for progress");
        deferred.reject("No url to check for progress");
      }
      if (firstTime) {
        return deferred.promise();
      }
    }
  });
  return Result;
});
