

define('scripts/views/user/login_dialog',['jquery', 'underscore', 'backbone', 'Handlebars', 'syphon', 'text!./login_dialog.hbs', 'ui_widgets/hold_please', 'ui_widgets/psst', 'composite_views/perch', 'bower_components_ext/bootstrap_buttons-radio'], function($, _, Backbone, Handlebars, Syphon, tmpl, holdPlease, psst, perch) {
  var LoginDialog, _confirmPassSel, _me, _registerBtnSel, _submitSel;
  _me = "views/user/login_dialog";
  _confirmPassSel = '#Login-confirm';
  _submitSel = '#Login-submit';
  _registerBtnSel = '#ActionRegister';
  LoginDialog = Backbone.View.extend({
    className: "loginDialog",
    events: {
      "click #SignInFacebook": "_clickedSignInFacebook",
      "click #ActionSignIn": "_modeSignIn",
      "click #ActionRegister": "_modeRegister",
      "click #Login-forgot": "_clickedForgotPass",
      "focus input": "_jazzifySubmitBtn",
      "submit": "_submittedForm"
    },
    initialize: function() {
      if (!((this.options.app != null) && (this.model != null))) {
        throw new Error('Need options.app and a model');
      }
      this.tmpl = Handlebars.compile(tmpl);
      this.listenTo(this.model, 'sync', this._onSync);
      this.listenTo(this.model, 'invalid', this._onModelInvalid);
      this.listenTo(this.model, 'error', this._onModelError);
      this._show();
      if (this.options.register) {
        this.$(_registerBtnSel).trigger('click');
        return this._modeRegister();
      }
    },
    render: function() {
      this.$el.html(this.tmpl());
      return this;
    },
    _show: function() {
      perch.show({
        content: this,
        btn1Text: null,
        supressTracking: true
      });
      return this.options.app.analytics.track(this.className, 'show');
    },
    _jazzifySubmitBtn: function() {
      return this.$(_submitSel).addClass('btn-inverse');
    },
    _showErr: function(msg) {
      msg = msg || 'Unknown Error';
      psst.hide();
      return psst({
        sel: "#LoginErrorHolder",
        msg: msg,
        type: 'error'
      });
    },
    _clickedSignInFacebook: function(e) {
      this.options.app.session.loginUsingOauth('facebook', {
        width: 1006,
        height: 775
      });
      holdPlease.show($(e.target));
      return this.options.app.analytics.track(this.className, 'Pressed Facebook Sign In');
    },
    _clickedForgotPass: function() {
      return console.log("" + _me + "._clickedForgotPass()");
    },
    _modeSignIn: function() {
      this._isRegisterMode = false;
      this.$(_confirmPassSel).hide();
      this._jazzifySubmitBtn();
      psst.hide();
      return this.options.app.analytics.track(this.className, 'modeSignIn');
    },
    _modeRegister: function() {
      this._isRegisterMode = true;
      this.$(_confirmPassSel).show();
      this._jazzifySubmitBtn();
      psst.hide();
      return this.options.app.analytics.track(this.className, 'modeRegister');
    },
    _submittedForm: function(e) {
      var formData;
      e.preventDefault();
      psst.hide();
      holdPlease.show(_submitSel);
      formData = Syphon.serialize(e.target);
      if (formData.loginType !== 'register') {
        formData.passwordConfirm = '';
      }
      this.options.app.session.logOut();
      if (formData.loginType === 'register') {
        console.log("" + _me + ".saving register form of user model...");
        this.model.set(formData);
        this.options.app.session.register();
      } else {
        this.model.set(formData, {
          silent: true
        });
        this.options.app.session.signIn();
      }
      return this.options.app.analytics.track(this.className, 'Submitted Form', formData.loginType);
    },
    _onSync: function(model, data) {
      return perch.hide();
    },
    _onModelInvalid: function(model, msg) {
      this._showErr(msg);
      holdPlease.hide(_submitSel);
      return this.options.app.analytics.track(this.className, 'Validation Issue');
    },
    _onModelError: function(model, xhr, options) {
      this._showErr("" + xhr.status + ": " + xhr.statusText);
      return holdPlease.hide(_submitSel);
    },
    hide: function() {
      return perch.hide();
    }
  });
  return LoginDialog;
});
