

define('core/mediator',['underscore', 'backbone', 'composite_views/perch', 'scripts/views/user/login_dialog', 'scripts/views/user/profile_dialog'], function(_, Backbone, perch, LoginDialog, ProfileDialog) {
  var Me, _me;
  _me = 'core/mediator';
  Me = Backbone.View.extend({
    initialize: function() {
      if (this.options.app == null) {
        throw new Error('Need options.app');
      }
      perch.options.app = this.options.app;
      return this._mediate();
    },
    _mediate: function() {
      this.listenTo(this.options.app, 'session:showLogin', this._showLogin);
      this.listenTo(this.options.app, 'session:showRegister', this._showRegister);
      this.listenTo(this.options.app, 'session:showProfile', this._showProfile);
      return this.listenTo(this.options.app, 'session:logOut', this._actionLogOut);
    },
    _showLogin: function() {
      var loginDialog;
      return loginDialog = new LoginDialog({
        model: this.options.app.user,
        app: this.options.app
      });
    },
    _showRegister: function() {
      var loginDialog;
      return loginDialog = new LoginDialog({
        model: this.options.app.user,
        app: this.options.app,
        register: true
      });
    },
    _showProfile: function() {
      var profileDialog;
      return profileDialog = new ProfileDialog({
        model: this.options.app.user
      });
    },
    _actionLogOut: function() {
      this.options.app.analytics.track('session', 'Pressed Log Out');
      return this.options.app.session.logOut();
    }
  });
  return Me;
});
