

define('controllers/session_controller',['jquery', 'underscore', 'backbone', 'Handlebars'], function($, _, Backbone, Handlebars) {
  var SessionController, _authUrlSuffix, _me;
  _me = 'controllers/session_controller';
  _authUrlSuffix = '/oauth/authorize';
  SessionController = function(options) {
    var _this = this;
    this.options = options;
    this.user = this.options.app.user;
    this.cfg = this.options.app.cfg;
    _.bindAll(this, 'onUserSync');
    this.user.on('sync', this.onUserSync);
    this._authUrl = "" + this.cfg.apiServer + _authUrlSuffix;
    $.ajaxSetup({
      type: 'POST',
      timeout: 5000,
      headers: {
        'X-CSRF-Token': $('meta[name="csrf-token"]').attr('content'),
        'Accept': 'application/json'
      },
      dataType: 'json',
      beforeSend: function(jqXHR, options) {
        var tokenHeader;
        if (_this.user.hasCurrentToken()) {
          tokenHeader = "Bearer " + (_this.user.get('accessToken'));
          return jqXHR.setRequestHeader('Authorization', tokenHeader);
        }
      }
    });
    if (this.user.hasCurrentToken()) {
      this.user.fetch();
    }
    return this;
  };
  SessionController.prototype = {
    signIn: function() {
      var deferred,
        _this = this;
      deferred = $.Deferred();
      if (this.user.hasCurrentToken() && !this.user.isGuest()) {
        this.user.fetch().done(function(data, textStatus, jqXHR) {
          return deferred.resolve("Success");
        }).fail(function(jqXHR, textStatus, errorThrown) {
          return deferred.reject(textStatus);
        });
      } else {
        $.ajax({
          url: this._authUrl,
          type: 'POST',
          data: {
            grant_type: "password",
            response_type: "password",
            email: this.user.get('email'),
            password: this.user.get('password'),
            client_id: this.cfg.appId,
            client_secret: this.cfg.appSecret
          }
        }).done(function(data, textStatus, jqXHR) {
          _this.user.set('accessToken', data.access_token);
          _this._persistLocally(data);
          return _this.user.fetch().done(function(data, textStatus, jqXHR) {
            _this.options.app.analytics.track('session', 'Successful Sign In');
            return deferred.resolve("Success");
          }).fail(function(jqXHR, textStatus, errorThrown) {
            _this.options.app.analytics.track('session', 'Failed Sign In');
            return deferred.reject(textStatus);
          });
        }).fail(function(jqXHR, textStatus, errorThrown) {
          console.log("" + _me + ".signIn().ajax().fail()");
          _this.user.trigger('error', _this.user, jqXHR);
          return deferred.reject(textStatus);
        });
      }
      return deferred.promise();
    },
    register: function() {
      var deferred,
        _this = this;
      deferred = $.Deferred();
      this.user.save().done(function(data, textStatus, jqXHR) {
        console.log("" + _me + ".user.save().done()");
        return _this.signIn().done(function() {
          _this.options.app.analytics.track('session', 'Successful Registration');
          return deferred.resolve("Success");
        }).fail(function() {
          _this.options.app.analytics.track('session', 'Failed Registration');
          return deferred.reject("Fail");
        });
      });
      return deferred.promise();
    },
    onUserSync: function(model) {
      var readableUserId;
      readableUserId = model.isGuest() ? model.attributes.id : model.attributes.email;
      return this.options.app.analytics.setUserIdentity(readableUserId);
    },
    _clearOutLocalStorage: function() {
      delete localStorage['access_token'];
      delete localStorage['expires_in'];
      delete localStorage['token_received'];
      delete localStorage['refresh_token'];
      return delete localStorage['guest'];
    },
    _buildExternalServiceUrl: function(authorizeThrough) {
      var authorize_param, guest_param, redirectUri, url;
      redirectUri = encodeURIComponent("" + window.location.protocol + "//" + window.location.host + "/redirect.html");
      authorize_param = "provider=" + authorizeThrough + "&";
      if (this.user.get('guest')) {
        guest_param = "guest_id=" + (this.user.get('id')) + "&";
      } else {
        guest_param = "";
      }
      url = "" + this._authUrl + "?" + authorize_param + guest_param + "client_id=" + this.cfg.appId + "&redirect_uri=" + redirectUri + "&response_type=token";
      return url;
    },
    _parseHash: function(hash) {
      var m, params, queryString, regex;
      params = {};
      queryString = hash.substring(1);
      regex = /([^&=]+)=([^&]*)/g;
      while ((m = regex.exec(queryString))) {
        params[decodeURIComponent(m[1])] = decodeURIComponent(m[2]);
      }
      return params;
    },
    _persistLocally: function(data) {
      localStorage['access_token'] = data['access_token'];
      localStorage['expires_in'] = data['expires_in'] * 1000;
      localStorage['token_received'] = new Date().getTime();
      return localStorage['refresh_token'] = data['refresh_token'];
    },
    logInAsGuest: function() {
      var deferred;
      deferred = $.Deferred();
      this.user.set({
        guest: true
      });
      this.register().done(function() {
        return deferred.resolve("Success");
      }).fail(function() {
        return deferred.reject("Fail");
      });
      return deferred.promise();
    },
    loginUsingOauth: function(provider, popupWindowSize) {
      var height, left, top, width;
      left = window.screenLeft + 50;
      top = window.screenTop + 50;
      width = popupWindowSize.width;
      height = popupWindowSize.height;
      window.OAuthRedirect = _.bind(this.externalAuthServiceCallback, this);
      return window.open(this._buildExternalServiceUrl(provider), "Login", "width=" + width + ", height=" + height + ", left=" + left + ", top=" + top + ", menubar=no");
    },
    logOut: function() {
      this._clearOutLocalStorage();
      return this.user.reset();
    },
    externalAuthServiceCallback: function(hash, location) {
      var params;
      params = this._parseHash(hash);
      if (params['access_token']) {
        this.logOut();
        this._persistLocally(params);
        this.user.reset();
        this.user.fetch();
      } else {
        console.log('Odd Error, no token received but redirected');
      }
      return this.options.app.analytics.track('session', 'Successful External Auth Login');
    }
  };
  return SessionController;
});
