

define('game/levels/circle_proximity/self_view',['backbone', 'text!./self.hbs'], function(Backbone, tmpl) {
  var Self, View, _me;
  Self = Backbone.Model.extend({
    defaults: {
      pxSize: 200,
      size: 200,
      top: 200,
      left: 0
    }
  });
  _me = 'game/levels/circle_proximity/self_view';
  View = Backbone.View.extend({
    id: 'SelfCircle',
    className: 'self',
    initialize: function() {
      return this.model = new Self();
    },
    render: function() {
      this.$el.html(tmpl);
      return this;
    },
    _setCssStyling: function(data) {
      return this.$el.css({
        top: data.top,
        left: data.left
      });
    },
    _calculateSelfCenter: function() {
      this._center = {
        x: this.$el.parent().width() / 2,
        y: this.model.get('top') + this.getSelfRadius()
      };
      return this._center;
    },
    getSelfCenter: function() {
      return this._center || this._calculateSelfCenter();
    },
    getSelfRadius: function() {
      return this._radius || this.model.get('pxSize') / 2;
    },
    position: function() {
      var left;
      left = this.getSelfCenter().x - this.model.get('pxSize') / 2;
      this.model.set({
        left: left
      });
      this._setCssStyling(this.model.attributes);
      return this;
    }
  });
  return View;
});
