

define('pages/play_game',['backbone', 'Handlebars', 'core', 'composite_views/perch', 'entities/levels', 'ui_widgets/steps_remaining', 'game/levels/reaction_time_disc', 'game/levels/rank_images', 'game/levels/circle_size_and_proximity', 'game/calculate_results'], function(Backbone, Handlebars, app, perch, LevelsCollection, StepsRemainingView, ReactionTime, ImageRank, CirclesTest, CalculateResultsView) {
  var Me, _defaultGame, _me, _stepsRemainingContainer, _views;
  _me = 'pages/playGame';
  _defaultGame = 1;
  _stepsRemainingContainer = '#HeaderRegion';
  _views = {
    'ReactionTime': 'ReactionTime',
    'ImageRank': 'ImageRank',
    'CirclesTest': 'CirclesTest'
  };
  Me = Backbone.View.extend({
    className: 'playGamePage',
    initialize: function() {
      this.curGame = app.user.createAssessment();
      return this._register_events();
    },
    _register_events: function() {
      this.listenTo(this.curGame, 'error', this._curGameErr);
      this.listenTo(this.curGame, 'sync', this._onGameSync);
      return this.listenTo(this.curGame, 'change:stage_completed', this._onStageChanged);
    },
    _showWelcome: function(assessmentModel) {
      this._trackLevels();
      return perch.show({
        title: 'Welcome',
        msg: assessmentModel.attributes.definition.instructions,
        btn1Text: 'Start',
        onClose: _.bind(this.curGame.nextStage, this.curGame),
        mustUseButton: true
      });
    },
    _onGameSync: function() {},
    _onStageChanged: function(model, stage) {
      var curStage, stageCount, _ref;
      curStage = model.attributes.stage_completed;
      stageCount = model.attributes.stages.length;
      if ((_ref = this.levels) != null) {
        _ref.setComplete(curStage);
      }
      if (curStage === -1) {
        return this._showWelcome(model);
      } else if (curStage < stageCount) {
        return this._showLevel(curStage);
      } else if (curStage === stageCount) {
        return this._calculateResults(model);
      } else {
        return console.log("" + _me + "._curGameSync: unusual curStage: " + curStage);
      }
    },
    _curGameErr: function() {
      console.log("" + _me + "._curGameErr()");
      return console.error("" + _me + ": Error on the curGame model");
    },
    _trackLevels: function() {
      this.levels = new LevelsCollection(this.curGame.get('stages'));
      this.stepsRemaining = new StepsRemainingView({
        collection: this.levels
      });
      return $(_stepsRemainingContainer).append(this.stepsRemaining.render().el);
    },
    _showLevel: function(stageId) {
      var ViewClass, curStage, viewClassString, _ref;
      curStage = this.curGame.get('stages')[stageId];
      viewClassString = _views[curStage.view_name];
      app.analytics.track(this.className, "game/1/level/" + stageId, 'levelName', viewClassString);
      ViewClass = eval(viewClassString);
      if ((_ref = this.curLevel) != null) {
        _ref.remove();
      }
      this.curLevel = new ViewClass({
        model: new Backbone.Model(curStage),
        assessment: this.curGame,
        stageNo: stageId
      });
      return this.$el.html(this.curLevel.render().el);
    },
    _calculateResults: function(gameModel) {
      this.curLevel = new CalculateResultsView({
        model: gameModel
      });
      return this.$el.html(this.curLevel.render().el);
    }
  });
  return Me;
});
