

define('game/levels/circle_size/sizey_view',['backbone', 'Handlebars', './circle_view', 'text!./sizey_view.hbs', 'jqueryui/slider'], function(Backbone, Handlebars, CircleView, tmpl, JqSlider) {
  var View, _circleSel, _me, _sliderSel;
  _me = 'game/levels/circle_proximity/sizey_view';
  _sliderSel = '.slider';
  _circleSel = '.circle';
  View = Backbone.View.extend({
    className: 'sizey',
    tmpl: Handlebars.compile(tmpl),
    initialize: function() {
      _.bindAll(this, 'onSlide');
      return this.circle = new CircleView({
        model: this.model
      });
    },
    render: function() {
      this.$el.html(this.circle.render().el);
      this.$el.append(this.tmpl(this.model.attributes));
      this.$el.find(_sliderSel).slider({
        value: this.model.get('size'),
        max: this.model.maxSize,
        slide: this.onSlide
      });
      return this;
    },
    _setWidthBySize: function(size) {
      return this.model.set({
        width: this.model.sizeToScale[size] * this.$(_circleSel).width()
      });
    },
    onSlide: function(e, ui) {
      this.model.set({
        size: ui.value
      });
      return this._setWidthBySize(ui.value);
    },
    close: function() {
      if (!this.model.get('width')) {
        this._setWidthBySize(this.model.get('size'));
      }
      this.remove();
      return this;
    }
  });
  return View;
});
