

define('entities/cur_user_personality',['backbone', 'core'], function(Backbone, app) {
  var Model, _me;
  _me = 'entities/cur_user_personality';
  Model = Backbone.Model.extend({
    url: "" + app.cfg.apiServer + "/api/v1/users/37/personality",
    initialize: function() {
      this.fetch();
      return this.on('error', this.onModelErr);
    },
    onModelErr: function() {
      return console.error("" + _me + ": Trouble getting the User Personality Model");
    }
  });
  return Model;
});
