

define('ui_widgets/psst',['jquery', 'backbone', 'Handlebars', "text!./psst.hbs"], function($, Backbone, Handlebars, tmpl) {
  var Me, me, _TYPES, _className, _defaultType, _me;
  _me = 'ui_widgets/psst';
  _TYPES = {
    good: 'good',
    info: 'info',
    question: 'question',
    warn: 'warn',
    error: 'error'
  };
  _defaultType = _TYPES.info;
  _className = 'psst';
  Me = Backbone.View.extend({
    tagName: 'aside',
    className: "" + _className + " alert",
    initialize: function() {
      return this.tmpl = Handlebars.compile(tmpl);
    },
    render: function(opts) {
      var options;
      if (!(opts.msg && opts.sel)) {
        throw new Error('Need .msg and .sel');
      }
      options = {
        title: opts.title || null,
        msg: opts.msg,
        type: opts.type || _defaultType,
        selector: opts.sel
      };
      this.$el.html(this.tmpl(options));
      this.$el.attr('class', "" + this.className + " " + options.type);
      this.$el.clone().prependTo(options.selector);
      return this;
    },
    _hide: function() {
      if (!this.$el.is(':visible')) {
        return;
      }
      this.$el.attr('class', this.className).remove();
      return this;
    },
    _hideAll: function() {
      return $("." + _className).remove();
    }
  });
  me = new Me();
  me.render = _.bind(me.render, me);
  me.render.hide = _.bind(me._hideAll, me);
  me.render.TYPES = _TYPES;
  return me.render;
});
