

define('entities/levels',['backbone'], function(Backbone) {
  var Collection, Model;
  Model = Backbone.Model.extend({
    defaults: {
      friendly_name: 'Default Level Name',
      isComplete: false
    },
    initialize: function() {
      var instructions, instructionsBrief;
      instructions = this.get('instructions');
      instructionsBrief = instructions instanceof Array ? instructions[0] : instructions;
      return this.set('instructionsBrief', instructionsBrief);
    }
  });
  Collection = Backbone.Collection.extend({
    model: Model,
    setComplete: function(LevelId) {
      var level;
      level = this.at(LevelId);
      if (level != null) {
        return level.set('isComplete', true);
      }
    }
  });
  return Collection;
});
