

define('pages/about',['backbone', 'Handlebars', 'text!./about.hbs'], function(Backbone, Handlebars, tmpl) {
  var Me;
  Me = Backbone.View.extend({
    className: 'aboutPage',
    initialize: function() {},
    render: function() {
      this.$el.html(tmpl);
      return this;
    }
  });
  return Me;
});
