

define('ui_widgets/proceed',['jquery', 'backbone', 'text!./proceed.hbs'], function($, Backbone, tmpl) {
  var Me, _me, _parentSel;
  _me = 'ui_widgets/proceed';
  _parentSel = 'body';
  Me = Backbone.View.extend({
    tagName: 'button',
    className: 'proceed',
    events: {
      "click": "onClick"
    },
    initialize: function() {
      return _.bindAll(this, 'show', 'hide');
    },
    render: function() {
      if (this.$el.is(':visible')) {
        return;
      }
      this.$el.html(tmpl);
      $(_parentSel).append(this.el);
      this.delegateEvents();
      this._animateIn();
      return this;
    },
    _animateIn: function() {
      var _this = this;
      this.$el.removeClass('bounceIn');
      return setTimeout(function() {
        return _this.$el.addClass('bounceIn', 1);
      });
    },
    onClick: function(e) {
      return this.trigger('click');
    },
    show: function() {
      return this.render();
    },
    hide: function() {
      return this.remove();
    }
  });
  return new Me();
});
