

define('game/calculate_results',['backbone', 'Handlebars', 'text!./calculate_results.hbs', 'core', 'composite_views/perch'], function(Backbone, Handlebars, tmpl, app, perch) {
  var Me, _me;
  _me = 'game/calculate_results';
  Me = Backbone.View.extend({
    className: 'calculateResults',
    initialize: function() {
      _.bindAll(this, '_gotGameResults', '_errGameResults');
      return this._getGameResults();
    },
    render: function() {
      this.$el.html(tmpl);
      return this;
    },
    _getGameResults: function() {
      var promise;
      app.analytics.track(this.className, 'Get game results');
      promise = this.model.getResult();
      promise.done(this._gotGameResults);
      return promise.fail(this._errGameResults);
    },
    _showResults: function() {
      if (app.user.isGuest()) {
        return app.router.navigate('guestSignup', {
          trigger: true
        });
      } else {
        return app.router.navigate('dashboard', {
          trigger: true
        });
      }
    },
    _gotGameResults: function(msg) {
      app.analytics.track(this.className, 'Successfully got game results');
      return setTimeout(this._showResults, 1500);
    },
    _errGameResults: function(msg) {
      app.analytics.track(this.className, 'Error Getting game results');
      return perch.show({
        title: 'Doh. Pretty Srs Error.',
        msg: msg,
        btn1Text: 'Ok',
        onClose: function() {
          return app.router.navigate('home', {
            trigger: true
          });
        },
        mustUseButton: true
      });
    }
  });
  return Me;
});
