

define('models/assessment',['jquery', 'backbone', 'models/result'], function($, Backbone, Result) {
  var Assessment, _me;
  _me = 'models/assessment';
  Assessment = Backbone.Model.extend({
    urlRoot: "" + window.apiServerUrl + "/api/v1/users/-/games",
    initialize: function() {
      return this;
    },
    parse: function(resp) {
      if (resp.result != null) {
        resp.result = new Result(resp.result, {
          parse: true
        });
      }
      return resp;
    },
    getLatestWithProfile: function() {
      var deferred,
        _this = this;
      deferred = $.Deferred();
      this.fetch({
        url: "" + (this.url()) + "/latest_with_profile"
      }).done(function(data, textStatus, jqXHR) {
        console.log('Got the latest assessment');
        return deferred.resolve();
      }).fail(function(jqXHR, textStatus, errorThrown) {
        return deferred.reject();
      });
      return deferred.promise();
    },
    getResult: function() {
      var deferred, result,
        _this = this;
      deferred = $.Deferred();
      if (this.get('result') != null) {
        deferred.resolve("Result already exists");
      } else {
        result = new Result({
          assessment_id: this.get('id')
        });
        this.set({
          result: result
        });
        if (this.get('status') === 'results_ready') {
          result = this.get('result');
          result.fetch().done(function(data, textStatus, jqXHR) {
            console.log("Get Result Success: " + textStatus);
            return deferred.resolve(jqXHR.response);
          }).fail(function(jqXHR, textStatus, errorThrown) {
            console.log("Get Result Error: " + textStatus);
            return deferred.reject(textStatus);
          });
        } else {
          result = this.get('result');
          result.calculateResult().done(function() {
            return deferred.resolve();
          }).fail(function() {
            return deferred.reject();
          });
        }
      }
      return deferred.promise();
    },
    create: function(gameId) {
      this.save({
        definition_id: gameId
      });
      return this;
    },
    nextStage: function() {
      var i;
      i = this.get('stage_completed');
      return this.save({
        stage_completed: i + 1
      });
    }
  });
  return Assessment;
});
