

define('entities/circles',['backbone'], function(Backbone) {
  var Collection, Model, _meCollection, _meModel;
  _meModel = 'game/levels/circle_proximity/circles Model';
  Model = Backbone.Model.extend({
    maxSize: 4,
    sizeToScale: [0.4, 0.7, 1.0, 1.3, 1.6],
    sizeToClass: ['size1', 'size2', 'size3', 'size4', 'size5'],
    defaults: {
      trait1: '',
      trait2: '',
      abbreviation: '',
      size: 2,
      selfProximityPx: null,
      selfProximityRank: null,
      selfOverlapPx: null,
      selfOverlapRatio: null,
      image: null,
      userChangedSize: false,
      userChangedPos: false,
      width: null,
      top: 0,
      left: 0,
      pos: {
        x: 0,
        y: 0
      }
    },
    initialize: function() {
      this._calcAbbreviation();
      this.set('size', 2);
      this.on('change:size', this.onChangeSize);
      return this.on('change:pos', this.onChangePos);
    },
    _calcAbbreviation: function() {
      var abbr, t1, t2;
      t1 = this.get('trait1').charAt(0);
      t2 = this.get('trait2').charAt(0);
      abbr = "" + t1 + "<em>/</em>" + t2;
      return this.set('abbreviation', abbr);
    },
    onChangeSize: function(model, sz) {
      return this.set('userChangedSize', true);
    },
    onChangePos: function(model, pos) {
      return this.set('userChangedPos', true);
    },
    getScale: function() {
      return this.sizeToScale[this.get('size')];
    }
  });
  _meCollection = 'game/levels/circle_proximity/circles Collection';
  Collection = Backbone.Collection.extend({
    model: Model
  });
  return Collection;
});
