

define('scripts/views/user/profile_dialog',['jquery', 'underscore', 'backbone', 'Handlebars', 'text!./profile_dialog.hbs', 'syphon', 'ui_widgets/hold_please', 'ui_widgets/psst', 'composite_views/perch', 'bower_components_ext/bootstrap_buttons-radio'], function($, _, Backbone, Handlebars, tmpl, Syphon, holdPlease, psst, perch) {
  var ProfileDialog, _errorHolderSel, _me, _submitBtnSel;
  _me = 'views/user/profile_dialog';
  _submitBtnSel = '[type=submit]';
  _errorHolderSel = '#ProfileErrorHolder';
  ProfileDialog = Backbone.View.extend({
    className: 'profileDialog',
    events: {
      'submit form': '_submitProfile',
      'input change': '_onInputChange'
    },
    initialize: function() {
      this.tmpl = Handlebars.compile(tmpl);
      this.listenTo(this.model, 'error', this._onError);
      this.listenTo(this.model, 'invalid', this._onInvalid);
      this.listenTo(this.model, 'sync', this.hide);
      return this._show();
    },
    render: function() {
      this.$el.html(this.tmpl(this.model.attributes));
      return this;
    },
    _show: function() {
      return perch.show({
        title: 'Profile',
        content: this,
        btn1Text: null
      });
    },
    _onInputChange: function() {
      return psst.hide();
    },
    _submitProfile: function(e) {
      var formData;
      console.log("" + _me + "._submitProfile()");
      e.preventDefault();
      psst.hide();
      holdPlease.show(this.$(_submitBtnSel));
      formData = Syphon.serialize(e.target);
      return this.model.save(formData, {
        wait: true,
        validateProfile: true
      });
    },
    _showErr: function(msg) {
      psst.hide();
      psst({
        sel: _errorHolderSel,
        msg: msg,
        type: 'error'
      });
      return holdPlease.hide(this.$(_submitBtnSel));
    },
    _onError: function(model, xhr) {
      var msg;
      if (xhr.status === 0) {
        msg = 'Unknown Server Error';
      } else {
        msg = "" + xhr.status + ": " + xhr.statusText;
      }
      return this._showErr(msg);
    },
    _onInvalid: function(model, msg) {
      return this._showErr(msg);
    },
    hide: function() {
      return perch.hide();
    }
  });
  return ProfileDialog;
});
