

define('core/main',['underscore', 'backbone', './config', './analytics', './mediator', './view', 'entities/user', 'controllers/session_controller', 'routers/main_router'], function(_, Backbone, config, Analytics, Mediator, View, User, SessionController, Router) {
  var Core, core, _me;
  _me = 'core/main';
  Core = function() {
    this.cfg = config;
    _.extend(this, Backbone.Events);
    this.user = new User();
    return this;
  };
  Core.prototype = {
    start: function() {
      this.session = new SessionController({
        app: this
      });
      this.user.session = this.session;
      this.analytics = new Analytics(this.cfg);
      this.view = new View({
        app: this
      });
      this.mediator = new Mediator({
        app: this
      });
      this.router = new Router(this);
      Backbone.history.start();
      return this;
    }
  };
  return core = new Core();
});

define('core', ['core/main'], function (main) { return main; });
