

define('game/levels/rank_images/main',['backbone', 'Handlebars', 'text!./main.hbs', './rankable_images', 'models/user_event', 'jqueryui/sortable', 'ui_widgets/proceed'], function(Backbone, Handlebars, tmpl, RankableImages, UserEvent, Sortable, proceed) {
  var View, _USEREVENTS, _me, _msgHtml, _rankingSel, _researchModuleName, _sortableSel, _unrankedSel;
  _me = 'game/levels/rank_images';
  _rankingSel = '#RankingArea';
  _unrankedSel = '#UnrankedArea';
  _sortableSel = '.connectedSortable';
  _msgHtml = '<li class="message">Rank from best to worst by clicking or dragging images.</li>';
  _researchModuleName = 'image_rank';
  _USEREVENTS = {
    started: "test_started",
    ranked: "image_ranked",
    unranked: "image_rank_cleared",
    dragged: "image_drag_start",
    completed: "test_completed"
  };
  View = Backbone.View.extend({
    className: 'rankImages',
    initialize: function() {
      this.collection = new RankableImages(this.model.get('image_sequence'));
      _.bindAll(this, 'onOver', 'onSortStart', 'onSortEnd', 'onUnrankedImageClick', 'onRankedImageClick', 'onProceedClick');
      this.listenTo(this.collection, 'change:rank', this.onRankChange);
      this.listenToOnce(proceed, 'click', this.onProceedClick);
      this.render();
      return this._trackStart();
    },
    render: function() {
      var _this = this;
      this.$el.html(tmpl);
      this.collection.each(function(imageModel) {
        return _this.$(_unrankedSel).append(imageModel.view.render().el);
      });
      this.$msg = $(_msgHtml);
      this._checkOnMsg();
      this.$(_sortableSel).sortable({
        placeholder: 'rankableImage placeholder',
        connectWith: _sortableSel,
        cancel: "." + (this.$msg.prop('class')),
        over: this.onOver,
        start: this.onSortStart,
        stop: this.onSortEnd
      });
      this.$(_sortableSel).disableSelection();
      this.$(_unrankedSel).on('click', '.rankableImage', this.onUnrankedImageClick);
      this.$(_rankingSel).on('click', '.rankableImage', this.onRankedImageClick);
      return this;
    },
    _checkOnMsg: function() {
      if (this.$(_rankingSel).children().length) {
        return this._hideMsg();
      } else {
        return this._showMsg();
      }
    },
    _showMsg: function() {
      return this.$(_rankingSel).append(this.$msg);
    },
    _hideMsg: function() {
      return this.$msg.remove();
    },
    _userTasksFinished: function() {
      return proceed.show();
    },
    _userTasksUnfinished: function() {
      return proceed.hide();
    },
    _checkOnRanks: function() {
      var rankedImages;
      this.collection.each(function(model) {
        if (model.view.$el.parent(_rankingSel).length) {
          return model.set({
            rank: model.view.$el.index() + 1
          });
        } else {
          return model.set({
            rank: model.defaults.rank
          });
        }
      });
      rankedImages = this.collection.filter(function(image) {
        return image.get('rank');
      });
      if (rankedImages.length === this.collection.length) {
        return this._userTasksFinished();
      } else {
        return this._userTasksUnfinished();
      }
    },
    onUnrankedImageClick: function(e) {
      this.$(_rankingSel).append(e.currentTarget);
      this._checkOnMsg();
      return this._checkOnRanks();
    },
    onRankedImageClick: function(e) {
      this.$(_unrankedSel).append(e.currentTarget);
      this._checkOnMsg();
      return this._checkOnRanks();
    },
    onProceedClick: function() {
      this._trackEnd();
      this.options.assessment.nextStage();
      return proceed.hide();
    },
    onOver: function() {
      return this._checkOnMsg();
    },
    onSortStart: function(e, ui) {
      var id;
      id = $(e.currentTarget).find('img').data('id');
      this._trackDragged(id);
      return ui.helper.appendTo('body');
    },
    onSortEnd: function(e, ui) {
      return this._checkOnRanks();
    },
    onRankChange: function(model, value, options) {
      if (value === -1) {
        return this._trackRankCleared(model.attributes.image_id, model.previous('rank'));
      } else {
        return this._trackRanked(model.attributes.image_id, value);
      }
    },
    _trackUserEvent: function(newEvent) {
      var eventInfo, userEvent;
      eventInfo = {
        game_id: this.options.assessment.get('id'),
        module: _researchModuleName,
        stage: this.options.stageNo
      };
      userEvent = new UserEvent();
      return userEvent.send(_.extend(eventInfo, newEvent));
    },
    _trackStart: function() {
      return this._trackUserEvent({
        event_desc: _USEREVENTS.started,
        image_sequence: this.collection.toJSON()
      });
    },
    _trackRanked: function(id, newRank) {
      return this._trackUserEvent({
        image_no: id,
        rank: newRank,
        event_desc: _USEREVENTS.ranked
      });
    },
    _trackRankCleared: function(id, oldRank) {
      return this._trackUserEvent({
        image_no: id,
        rank: oldRank,
        event_desc: _USEREVENTS.unranked
      });
    },
    _trackDragged: function(id) {
      return this._trackUserEvent({
        image_no: id,
        event_desc: _USEREVENTS.dragged
      });
    },
    _trackEnd: function() {
      return this._trackUserEvent({
        event_desc: _USEREVENTS.completed
      });
    }
  });
  return View;
});

define('game/levels/rank_images', ['game/levels/rank_images/main'], function (main) { return main; });
