

define('ui_widgets/steps_remaining',['jquery', 'backbone', 'Handlebars', "text!./steps_remaining.hbs"], function($, Backbone, Handlebars, tmpl) {
  var View;
  View = Backbone.View.extend({
    className: 'stepsRemaining',
    initialize: function() {
      if (!this.collection) {
        throw new Error('Need a collection to build a stepsRemaining view');
      }
      this.tmpl = Handlebars.compile(tmpl);
      return this.listenTo(this.collection, 'change', this._onChange);
    },
    render: function() {
      this.$el.html(this.tmpl({
        steps: this.collection.toJSON()
      }));
      return this;
    },
    _onChange: function() {
      return this.render();
    }
  });
  return View;
});
