

define('models/user_event',['underscore', 'backbone'], function(_, Backbone) {
  var UserEvent, _me;
  _me = 'models/user_event';
  UserEvent = Backbone.Model.extend({
    urlRoot: "/api/v1/user_events",
    initialize: function() {
      return this.url = window.apiServerUrl + this.urlRoot;
    },
    send: function(attribs) {
      var defaultInfo, eventInfo, timestamp;
      timestamp = new Date().getTime();
      defaultInfo = {
        "record_time": timestamp
      };
      eventInfo = _.extend({}, defaultInfo, attribs);
      return this.save(eventInfo, {
        error: function() {
          return console.log("" + _me + ".save().error()");
        },
        success: function(model) {}
      });
    }
  });
  return UserEvent;
});
