

define('pages/home',['backbone', 'Handlebars', 'text!./home.hbs'], function(Backbone, Handlebars, tmpl) {
  var Me;
  Me = Backbone.View.extend({
    className: 'homePage',
    tmpl: Handlebars.compile(tmpl),
    render: function() {
      this.$el.html(this.tmpl());
      return this;
    }
  });
  return Me;
});
