

define('game/levels/circle_size_and_proximity',['jquery', 'underscore', 'backbone', 'Handlebars', 'models/user_event', 'entities/circles', 'game/levels/circle_size', 'game/levels/circle_proximity'], function($, _, Backbone, Handlebars, UserEvent, Circles, CircleSize, CircleProximity) {
  var View, _USEREVENTS, _me, _researchModuleName;
  _me = 'game/levels/circle_size_and_proximity';
  _researchModuleName = 'circles_test';
  _USEREVENTS = {
    started: 'test_started',
    startedSizing: 'size_circles_started',
    startedProximity: 'move_circles_started',
    completed: 'test_completed'
  };
  View = Backbone.View.extend({
    className: 'circleSizeAndProximity',
    initialize: function(options) {
      this.track({
        event_desc: _USEREVENTS.started
      });
      this.circlesCollection = new Circles(this.model.get('circles'));
      return _.bindAll(this, 'track');
    },
    render: function() {
      this._showCircleSize();
      return this;
    },
    _showCircleSize: function() {
      this.curView = new CircleSize({
        collection: this.circlesCollection,
        runner: this
      });
      this.$el.html(this.curView.el);
      this.track({
        event_desc: _USEREVENTS.startedSizing
      });
      this.circlesCollection = this.curView.collection;
      return this.listenToOnce(this.curView, 'done', this._showCircleProximity);
    },
    _showCircleProximity: function() {
      this.curView = new CircleProximity({
        collection: this.circlesCollection,
        runner: this
      });
      this.$el.html(this.curView.el);
      this.track({
        event_desc: _USEREVENTS.startedProximity,
        circles: this.circlesCollection.toJSON()
      });
      return this.listenToOnce(this.curView, 'done', this.onTestDone);
    },
    onTestDone: function() {
      this.remove();
      this.track({
        event_desc: _USEREVENTS.completed,
        circles: this.circlesCollection.toJSON(),
        self_coord: {
          top: this.curView.selfView.getSelfCenter().y - this.curView.selfView.getSelfRadius(),
          left: this.curView.selfView.getSelfCenter().x - this.curView.selfView.getSelfRadius(),
          size: this.curView.selfView.getSelfRadius() * 2
        }
      });
      return this.options.assessment.nextStage();
    },
    track: function(newEvent) {
      var eventInfo, userEvent;
      eventInfo = {
        game_id: this.options.assessment.get('id'),
        module: _researchModuleName,
        stage: this.options.stageNo
      };
      userEvent = new UserEvent();
      return userEvent.send(_.extend(eventInfo, newEvent));
    }
  });
  return View;
});
